.card {
  background: #fff;
  border-radius: 2px;
  height: 200px;
  margin: 1rem;
  position: relative;
  width: 250px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background:#6CADDC;
}
.card > .img-container > img {
  width: 60%
}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}

.card > .content > ul {
  list-style-type: none;
}

.card > .content > ul > li {
  margin-top: 18px;
}



.card > .remove:hover {
  color: #d50000;
}
.green {
  color: orange;
  font-size: 1.2em
}
.red{
  color: red;
  font-size: 1.2em
}

#header{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  text-align: center;
  background-color:darkblue;
  color: white;
  font-size: 2em;
  padding: 15px;
}

#friend-cards {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr ;
  
}


.title {
  width: 100%;
  text-align: center;
  margin-bottom: 50px;
}

* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

